import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const MIN = 1;
const MAX = 5;

const initialState = {
	aiData: wutil.makeRandomData(300, MIN, MAX, true),
	windData: wutil.makeRandomData(300, MIN, MAX, true),
};

@observer
class Wear extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Wear status</h1>
						<LineGraph 
							data={windData}
							dataB={aiData}
							legend='Wear status'
							legendB='Wear status AI prediction'
							width={1200}
							height={500}
							yMax={1000}
							y2BMax={1000}
							showYTicks={1}
							yTicks='["100%", "75%", "50%", "25%", "0%"]'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Wear;
